$blue: #006cd9ff;
$black: #181f25ff;

$text-primary-color: #3d464dff;
$text-secondary-color: #525d66ff;
$text-tertiary-color: #708090ff;

$text-primary-size: 14px;
$text-primary-font-family: "IBM Plex Sans", sans-serif;

$state-active: #f2faffff;

@mixin transition {
  transition: all 0.3s;
}

@mixin container($name, $width) {
  @container #{$name} (max-width: #{$width}) {
    @content;
  }

  @media (max-width: $width) {
    @content;
  }
}

@mixin desktop-only {
  @media (min-width: 1251px) {
    @content;
  }
}

@mixin notebook {
  @media (max-width: 1250px), (max-device-height: 767px) {
    @content;
  }
}

@mixin tablet {
  @media (max-width: 767px) {
    @content;
  }
}

@mixin tablet-container($name) {
  @include container($name, 767px) {
    @content;
  }
}

@mixin phone {
  @media (max-width: 380px) {
    @content;
  }
}

@mixin dark {
  html.dark & {
    @content;
  }
}

:root {
  --primary: #{$primary};

  --bg: #{$bg};
  --bg-transparent: #{$bg-transparent};
  --secondary-bg: #{$secondary-bg};

  --tertiary-bg: #{$tertiary-bg};
  --tertiary-accent: #{$tertiary-accent};

  --light-primary: #{$light-primary};
  --lighter-primary: #{$lighter-primary};

  --text-primary: #{$text-primary};
  --text-primary-font-family: #{$text-primary-font-family};
  --text-secondary: #{$text-secondary};
  --text-tertiary: #{$text-tertiary};
  --text-tertiary-accent: #{$text-tertiary-accent};

  --text-success: #007f00;
  --text-warning: #eab106;
  --text-error: #ca0000;

  --bg-warning: #{$bg-warning};
  --bg-success: #{$bg-success};
  --bg-error: #{$bg-error};

  --shadow-color: #{$shadow-color};
  --shadow-color-1: #{$shadow-color-1};
  --img-filter: #{$img-filter};
  --img-filter-inverted: #{$dark-img-filter};

  --yellow-gradient: #{$yellow-gradient};
  --yellow-border: #{$yellow-border};
  --light-yellow-gradient: #{$light-yellow-gradient};
  --purple-gradient: #{$purple-gradient};
  --purple-border: #{$purple-border};
  --pink-gradient: #{$pink-gradient};
  --pink-border: #{$pink-border};

  --stub-bg: #{$stub-bg};

  --body-top-border-gradient: #{$body-top-border-gradient};
  --navigation-bg: #{$navigation-bg};

  --footer-top-bg: #{$footer-top-bg};
  --footer-bottom-bg: #{$footer-bottom-bg};

  --news-article-gradient: #{$news-article-gradient};
  --news-sidebar-gradient: #{$news-sidebar-gradient};
  --news-active-color: #{$news-active-color};

  --my-heading-bg: #{$my-heading-background};
  --my-device-shadow: #{$my-device-shadow};

  --configure-error-text: #{$configure-error-text};

  --request-button-bg: #{$request-button-bg};
  --request-plus-bg: #{$request-plus-bg};

  --info-sidebar-gradient: #{$info-sidebar-gradient};

  --app-fadeout-gradient: #{$app-fadeout-gradient};

  --modal-overlay-opacity: #{$modal-overlay-opacity};
}

:root.dark {
  --bg: #{$dark-bg};
  --bg-transparent: #{$dark-bg-transparent};
  --secondary-bg: #{$dark-secondary-bg};

  --tertiary-bg: #{$dark-tertiary-bg};
  --tertiary-accent: #{$dark-tertiary-accent};

  --light-primary: #{$dark-light-primary};
  --lighter-primary: #{$dark-lighter-primary};

  --text-primary: #{$dark-text-primary};
  --text-secondary: #{$dark-text-secondary};
  --text-tertiary: #{$dark-text-tertiary};
  --text-tertiary-accent: #{$dark-text-tertiary-accent};

  --text-success: #009a00;
  --text-warning: #ffc107;
  --text-error: #ff7070;

  --stub-bg: #{$dark-stub-bg};

  --bg-warning: #{$dark-bg-warning};
  --bg-success: #{$dark-bg-success};
  --bg-error: #{$dark-bg-error};

  --shadow-color: #{$dark-shadow-color};
  --shadow-color-1: #{$dark-shadow-color-1};
  --img-filter: #{$dark-img-filter};
  --img-filter-inverted: #{$img-filter};

  --yellow-gradient: #{$dark-yellow-gradient};
  --yellow-border: #{$dark-yellow-border};
  --light-yellow-gradient: #{$dark-light-yellow-gradient};
  --purple-gradient: #{$dark-purple-gradient};
  --purple-border: #{$dark-purple-border};
  --pink-gradient: #{$dark-pink-gradient};
  --pink-border: #{$dark-pink-border};

  --body-top-border-gradient: #{$dark-body-top-border-gradient};
  --navigation-bg: #{$dark-navigation-bg};

  --footer-top-bg: #{$dark-footer-top-bg};
  --footer-bottom-bg: #{$dark-footer-bottom-bg};

  --news-article-gradient: #{$dark-news-article-gradient};
  --news-sidebar-gradient: #{$dark-news-sidebar-gradient};
  --news-active-color: #{$dark-news-active-color};

  --my-heading-bg: #{$dark-my-heading-background};
  --my-device-shadow: #{$dark-my-device-shadow};

  --configure-error-text: #{$dark-configure-error-text};

  --request-button-bg: #{$dark-request-button-bg};
  --request-plus-bg: #{$dark-request-plus-bg};

  --info-sidebar-gradient: #{$dark-info-sidebar-gradient};

  --app-fadeout-gradient: #{$dark-app-fadeout-gradient};

  --modal-overlay-opacity: #{$dark-modal-overlay-opacity};
}
